import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import {
  loginUser,
  signUpUser,
} from "./authService";

export interface AuthState {
  loading: boolean;
  user: any;
  error: any;
  signUpResponse: any;
}
const userData: string | null = localStorage.getItem("user");
const initialState: AuthState = {
  loading: false,
  user: userData !== null ? JSON.parse(userData) : {},
  error: null,
  signUpResponse: {},
};

export const authSlice = createSlice({
  name: "app",
  initialState,
  reducers: {
    setLoading: (state: AuthState, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    updateUserDetails: (state: AuthState, action: PayloadAction<boolean>) => {
      state.user = action.payload;
    },
    updateErrorMessage: (state: AuthState, action: PayloadAction<boolean>) => {
      state.error = action.payload;
    },
  },
  extraReducers: (builder: any) => {
    builder.addCase(loginUser.pending, (state: any) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(
      loginUser.fulfilled,
      (state: AuthState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        // console.log("action", action);
        state.user = action.payload;
        var userInfo = action.payload;
        
        localStorage.setItem("user", JSON.stringify(userInfo));
      }
    );
    builder.addCase(loginUser.rejected, (state: AuthState, action: any) => {
      state.loading = false;
      // console.log("action", action);
      state.user = {};
      state.error = action.error?.message ?? "Something went wrong";
    });


    builder.addCase(signUpUser.pending, (state: any) => {
      state.loading = true;
      state.error = null;
      state.signUpResponse = null;
    });
    builder.addCase(
      signUpUser.fulfilled,
      (state: AuthState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.signUpResponse = action.payload;
        state.user = action.payload;
        var userInfo = action.payload;
        
        localStorage.setItem("user", JSON.stringify(userInfo));

      }
    );
    builder.addCase(signUpUser.rejected, (state: AuthState, action: any) => {
      console.log({ action });
      state.loading = false;
      state.signUpResponse = null;
      state.error = action.error?.message ?? "Something went wrong";
    });



    
  },
});

export const { setLoading , updateUserDetails, updateErrorMessage} = authSlice.actions;

export default authSlice.reducer;
