import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import {
  getallProducts,
  createProductCall,
  getProductById,
  placeOrderCall,
  getOrderDetails,
  getallOrderCall,
  getallOrdersCall,
  getAllUsers,
 
} from "./productCartService";

export interface productCartState {
  loading: boolean;
  error: any;
  allProducts: [];
  productDetails:{};
  productDetailsResponse: {};
  cartItems:[];
  orderDetailsData:{};
  allOrderData:[],
  allUserOrdersData:[],
  allUsersList:[],
}
const cartData: string | null = localStorage.getItem("cartItems");
const initialState: productCartState = {
  loading: false,
  error: null,
  allProducts: [],
  productDetails:{},
  productDetailsResponse:{},
  orderDetailsData:{},
  cartItems: cartData !== null ? JSON.parse(cartData) : [],
  allOrderData:[],
  allUserOrdersData:[],
  allUsersList:[],
};

export const authSlice = createSlice({
  name: "productCart",
  initialState,
  reducers: {
    setLoading: (state: productCartState, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setErrorMessage: (state: productCartState, action: PayloadAction<boolean>) => {
      state.error = action.payload;
    },
    setallProductsData: (state: productCartState, action: PayloadAction<any>) => {
      state.allProducts = action.payload;
    },
    setcartItemsData: (state: productCartState, action: PayloadAction<any>) => {
      state.cartItems = action.payload;
      localStorage.setItem("cartItems", JSON.stringify(action.payload));
    },
    setproductDetailsResponse: (state: productCartState, action: PayloadAction<any>) => {
      state.productDetailsResponse = action.payload;
    }

    
  },
  extraReducers: (builder: any) => {
    //delete product from cart
    builder.addCase(getallProducts.pending, (state: any) => {
      state.loading = true;
      state.error = null;
      state.allProducts = [];
    });
    builder.addCase(
      getallProducts.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.allProducts = action?.payload?.data;
      }
    );
    builder.addCase(
      getallProducts.rejected,
      (state: productCartState, action: PayloadAction<any>) => {
        state.loading = false;
        state.error = action.payload;
        state.allProducts = [];
      }
    );


    builder.addCase(createProductCall.pending, (state: any) => {
      state.loading = true;
      state.error = null;
      state.productDetailsResponse = {};
    });
    builder.addCase(
      createProductCall.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.productDetailsResponse = action?.payload?.data;
      }
    );
    builder.addCase(createProductCall.rejected, (state: productCartState, action: any) => {
      console.log({ action });
      state.loading = false;
      console.log("action", action)
      state.error = action.error?.message ?? "Something went wrong";
    });

    builder.addCase(getProductById.pending, (state: any) => {
      state.loading = true;
      state.error = null;
      state.productDetails = {};
      state.productDetailsResponse = {};
    });
    builder.addCase(
      getProductById.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.productDetails = action?.payload?.data;
        // state.productDetailsResponse = action?.payload?.data;
      }
    );
    builder.addCase(getProductById.rejected, (state: productCartState, action: any) => {
      console.log({ action });
      state.loading = false;
      console.log("action", action)
      state.error = action.error?.message ?? "Something went wrong";
    });

    builder.addCase(placeOrderCall.pending, (state: any) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(
      placeOrderCall.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        // state.productDetailsResponse = action?.payload?.data;
      }
    );
    builder.addCase(placeOrderCall.rejected, (state: productCartState, action: any) => {
      console.log({ action });
      state.loading = false;
      state.error = action.error?.message ?? "Something went wrong";
    });


    builder.addCase(getOrderDetails.pending, (state: any) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(
      getOrderDetails.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.orderDetailsData = action?.payload?.data;
      }
    );
    builder.addCase(getOrderDetails.rejected, (state: productCartState, action: any) => {
      console.log({ action });
      state.loading = false;
      state.error = action.error?.message ?? "Something went wrong";
    });

    builder.addCase(getallOrderCall.pending, (state: any) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(
      getallOrderCall.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.allOrderData = action?.payload?.data;
      }
    );
    builder.addCase(getallOrderCall.rejected, (state: productCartState, action: any) => {
      console.log({ action });
      state.loading = false;
      state.error = action.error?.message ?? "Something went wrong";
    });


    builder.addCase(getallOrdersCall.pending, (state: any) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(
      getallOrdersCall.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.allUserOrdersData = action?.payload?.data;
      }
    );
    builder.addCase(getallOrdersCall.rejected, (state: productCartState, action: any) => {
      console.log({ action });
      state.loading = false;
      state.error = action.error?.message ?? "Something went wrong";
    });

    builder.addCase(getAllUsers.pending, (state: any) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(
      getAllUsers.fulfilled,
      (state: productCartState, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.allUsersList = action?.payload?.users;
      }
    );
    builder.addCase(getAllUsers.rejected, (state: productCartState, action: any) => {
      console.log({ action });
      state.loading = false;
      state.error = action.error?.message ?? "Something went wrong";
    });
    

  }
});

export const { setLoading , setcartItemsData,setproductDetailsResponse, setallProductsData} = authSlice.actions;

export default authSlice.reducer;
