import React, { useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import Home from "../pages/Home";
import Cart from "../pages/Cart";
import Checkout from "../pages/Checkout";
import Signin from "../pages/Signin";
import CreateProduct from "../pages/CreateProduct";
import ManageProducts from "../pages/ManageProducts";
import { useLocation } from "react-router-dom";
import ProductsSection from "../pages/ProductsSection";
import MyProfile from "../pages/MyProfile";
import AdminAccount from "../pages/AdminAccount";
import OrderSummary from "../pages/OrderSummary";
import ContactUS from "../pages/ContactUS";
import ToolDetails from "../pages/ToolDetails";
import Courses from "../pages/Courses/Courses";
import AboutUs from "../pages/AboutUs/AboutUs";
import TripDetails from "../pages/TripDetails/TripDetails";
import AllTrips from "../pages/AllTrips/AllTrips";
import UpSkillPrograms from "../pages/UpSkillPrograms/UpSkillPrograms";
import FinTechProgram from "../pages/FinTechProgram/FinTechProgram";
import PythonOnlineProgram from "../pages/PythonOnlineProgram/PythonOnlineProgram";
import OurExperts from "../pages/OurExperts/OurExperts";
import LearningResources from "../pages/LearningResources/LearningResources";
import ProductorWise from "../pages/UpSkillPrograms/ProductorWise";
// TripDetails

const MainRoutes: React.FC = () => {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return (
    <Switch>
      {/*Route exact path="/" component={Home} />*/}
      <Route exact path="/" component={Home} />
      <Route exact path="/singapore-trip" component={TripDetails} />
      <Route exact path="/all-trips" component={AllTrips} />
      <Route exact path="/upskill-programs" component={UpSkillPrograms} />
      <Route
        exact
        path="/certificateProgramonFinTech"
        component={FinTechProgram}
      />
      <Route
        exact
        path="/pythonOnlineProgram"
        component={PythonOnlineProgram}
      />
      <Route exact path="/ourExperts" component={OurExperts} />
      <Route exact path="/learning-resources" component={LearningResources} />

      <Route exact path="/products" component={ProductsSection} />

      {/* <Route exact path="/" component={AboutUs} /> */}
      <Route exact path="/home" component={AboutUs} />
      <Route exact path="/courses" component={Courses} />
      <Route exact path="/aboutUs" component={AboutUs} />
      <Route exact path="/tools" component={Home} />
      <Route exact path="/signin" component={Signin} />
      <Route exact path="/myProfile" component={MyProfile} />
      <Route exact path="/contactus" component={ContactUS} />
      <Route exact path="/details/:toolNmae" component={ToolDetails} />

      <Route exact path="/cart" component={Cart} />
      <Route exact path="/checkout" component={Checkout} />
      <Route exact path="/admin/createProduct" component={CreateProduct} />
      <Route exact path="/admin/editProduct/:id" component={CreateProduct} />
      <Route exact path="/admin/manageProducts" component={ManageProducts} />

      <Route exact path="/admin" component={AdminAccount} />
      <Route exact path="/orderSummary/:id" component={OrderSummary} />
      <Route exact path="/ProductorWise" component={ProductorWise} />
    </Switch>
  );
};

export default MainRoutes;
