import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import {
    addAddressCall,
    getallAddress
} from "./addressService";

export interface Addresstate {
  loading: boolean;
  error: any;
  allAddress:[];
  newAddress:{},
}
const userData: string | null = localStorage.getItem("user");
const initialState: Addresstate = {
  loading: false,
  error: null,
  allAddress: [],
  newAddress:{},
};

export const addressSlice = createSlice({
  name: "address",
  initialState,
  reducers: {
    setLoading: (state: Addresstate, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setErrorMessage: (state: Addresstate, action: PayloadAction<boolean>) => {
      state.error = action.payload;
    },
  },
  extraReducers: (builder: any) => {
    //delete product from cart
    builder.addCase(getallAddress.pending, (state: any) => {
      state.loading = true;
      state.error = null;
      state.allProducts = [];
    });
    builder.addCase(
        getallAddress.fulfilled,
      (state: Addresstate, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.allAddress = action?.payload?.data;
      }
    );
    builder.addCase(
        getallAddress.rejected,
      (state: Addresstate, action: PayloadAction<any>) => {
        state.loading = false;
        state.error = action.payload;
        state.allAddress = [];
      }
    );


    builder.addCase(addAddressCall.pending, (state: any) => {
      state.loading = true;
      state.error = null;
      state.productDetailsResponse = {};
    });
    builder.addCase(
      addAddressCall.fulfilled,
      (state: Addresstate, action: PayloadAction<any>) => {
        state.error = null;
        state.loading = false;
        state.newAddress = action?.payload?.data;
      }
    );
    builder.addCase(addAddressCall.rejected, (state: Addresstate, action: any) => {
      console.log({ action });
      state.loading = false;
      console.log("action", action)
      state.error = action.error?.message ?? "Something went wrong";
    });


  }
});

export const { setLoading } = addressSlice.actions;

export default addressSlice.reducer;
