import React, { useState, useContext } from "react";
import "./styles.css";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";

import { setcartItemsData } from "../../redux/slices/productCart/productCartSlice";
import { Row, Col, Button, Form, CardDeck, CardColumns } from "react-bootstrap";
import { deleteProduct, getallProducts } from "../../redux/slices/productCart/productCartService";

import { getProductPrice } from "../../Scenes/common";

const ProductItem = (props) => {
  const { product, onDeleteClick, fromCart, fromHome, updateStatus } = props;
  const { cartItems } = useSelector(({ productCart }) => productCart);
  const dispatch = useDispatch();
  const history = useHistory();
  console.log("cartItems", cartItems);


  const editButtonClick = (product) => {
    if (product?._id) {
      history.push(`/admin/editProduct/${product?._id}`)
    }
  }


  return (<>
    <div className="product_cart managecartitems">
      <div className="cart_product-image">
        <img src={product?.imageurl} alt="Image" />
      </div>
      <div className="cart_product-name common_cart_cls">{product?.title}</div>
      <div className="cart_product-price1 common_cart_cls">
        {/* {getProductPrice(product)} */}
        {product?.priceList.map((itemp) => {
          return (
            <div>
              {itemp?.city}- {itemp?.client} : {itemp?.price}({itemp?.discount}%) / {product?.priceType}
            </div>
          )
        })}
      </div>
      <div className="ProductItemActions">

        <Button onClick={() => { editButtonClick(product) }} className="editBtn">Edit</Button>
        <Button onClick={() => { onDeleteClick(product) }} className="deleteBtn"> Delete</Button>
        <div class="custom-control custom-switch">
          <input type="checkbox" onChange={(e) => { updateStatus(e, product) }} class="custom-control-input" id="customSwitch1" checked={product?.status} />
          <label class="custom-control-label" for="customSwitch1"></label>
        </div>
      </div>



    </div>
  </>
  );
};

export default ProductItem;
