

import React, { useEffect, useState } from "react";

import { Row, Col, Button, Form, CardDeck, CardColumns } from "react-bootstrap";
import "./styles.css";
import { ReactComponent as ImagePlaceholderIcon } from "../../assets/ImagePlaceholder.svg"
import { ReactComponent as DeleteBin } from "../../assets/deleteBin.svg";

import { useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
// import ProductCard from "./../../components/ProductCard";
import { createProductCall, getProductById } from "../../redux/slices/productCart/productCartService";
import { setproductDetailsResponse } from "../../redux/slices/productCart/productCartSlice";
import { setallProductsData } from "../../redux/slices/productCart/productCartSlice";
import CustomModal from "../../components/CustomModel";
import { getAllUsers } from "../../redux/slices/productCart/productCartService";


const initVal = {
    title: undefined,
    description: undefined,
    // price: undefined,
    priceType: undefined,
    category:undefined,
};

const initValError = {
    title: false,
    description: false,
    // price: false,
    priceType: false,
    category: false,
};

const initValErrorData = {
    title: "Please enter Title",
    description: "Please enter Description",
    // price: "Please enter Price",
    priceType: "Please select price Type",
    category: "Please select Category"
};
const initialPriceobj = { city: "", client: "", price: "", discount: "", cityError: false, clientError: false, priceError: false, discountError: false }
const CreateProduct = (props) => {

    const [addressEnabled, setAddressEnabled] = useState(false)
    const history = useHistory();
    const dispatch = useDispatch();
    const { allUsersList, productDetails, productDetailsResponse, loading } = useSelector(({ productCart }) => productCart);

    const [productData, setProductData] = useState(initVal);
    const [errorData, setErrorData] = useState(initValError);
    const [errorDataValue, setErrorDataValue] = useState(initValErrorData);
    const [coverImage, setCoverImage] = useState(null);
    const [errorImage, setErrorImage] = useState(null);
    const [allCitysList, setAllCitysList] = useState([]);
    const [allClientList, setAllClientList] = useState([]);
    const [productSuccess, setProductSuccess] = useState(false);
    const [inputList, setInputList] = useState([{ city: "", client: "", price: "", discount: "", cityError: false, clientError: false, priceError: false, discountError: false }]);
    const [textHeader, setTextHeader] = useState("Product Created Successfully");
    const allPriceType = [
        { id: "Kg", value: "Kg" },
        { id: "Ton", value: "Ton" },
        { id: "Bottle", value: "Bottle" },
        { id: "Item", value: "Item" },
    ]

    console.log("productDetailsResponse", productDetailsResponse);

    useEffect(() => {
        if (productDetailsResponse?._id) {
            setTextHeader("Product Created Successfully.")
            setProductSuccess(true);
        } else if (props?.match?.params?.id && productDetailsResponse?.nModified) {
            setProductSuccess(true);
            setTextHeader("Product Updated Successfully.")
        }
    }, [productDetailsResponse]);

    useEffect(() => {
        if (productDetails?._id) {
            const productData = {
                title: productDetails?.title,
                description: productDetails?.description,
                priceType: productDetails?.priceType,
                category: productDetails?.category,
                
            }
            setProductData(productData);
            setCoverImage(productDetails?.imageurl)
            setInputList(productDetails?.priceList)
        }
    }, [productDetails]);


    useEffect(() => {
        if (allUsersList) {
            var city1 = [];
            var client1 = [];
            allUsersList?.map((user, index)=>{
                if(user?.companyName && user?.city){
                var city_sub = {id: index, name: user?.city, city: user?.city, client: user?.companyName}
                var client_sub = {id: index, name: user?.companyName, city: user?.city, client: user?.companyName}
                city1.push(city_sub);
                client1.push(client_sub);
                }
            });
            setAllCitysList(city1);
            setAllClientList(client1);
            console.log("allUsersList", allUsersList);
        }
    }, [allUsersList]);
    
    console.log("allCitysList", allCitysList);
    console.log("allClientList", allClientList);


    useEffect(() => {
        // const newSet  = priceList;
        // setPriceList(newSet.push(0));
        dispatch(getAllUsers());
        setProductData(initVal)
        if (props?.match?.params?.id) {
            dispatch(getProductById(props?.match?.params?.id))
        }
        console.log("propsprops555", props);
    }, []);

    // 

    const handleChangeValue = (e) => {
        let { name, value } = e.target;
        var value1 = value;
        if (value.trim() == "") {
            value1 = value.trim();
        }
        if (name === "price") {
            value1 = value1.replace(/[^\d]/g, "")
        }

        setErrorImage(null);
        const addr = { ...productData };
        const addr_error = { ...errorData };
        setErrorData({
            ...addr_error,
            [name]: false,
        });
        setProductData({
            ...addr,
            [name]: value1,
        });

    }
    const createAccountData = () => {
        var isValid = true;
        var errornew = { ...errorData };
        for (var key in productData) {
            if (productData[key] === "" || !productData[key]) {
                errornew[key] = true;
                isValid = false;
            }
        }
        console.log("errornew", errornew)
        setErrorData({ ...errornew });
        if (isValid && coverImage && priceSectionValidate()) {
            makecreateProductCall(productData);
            console.log("productData", productData)
        } else if (!coverImage) {
            setErrorImage("Please upload valid Image")
        }
    }
    const makecreateProductCall = async (data) => {
        console.log("datadata", data);
        const body = {
            "title": data?.title,
            "description": data?.description,
            "imageurl": coverImage,
            "status": true,
            // "price": data?.price,
            "priceType": data?.priceType,
            "category": data?.category,
            // "priceType": "KG"
            "priceList": [...inputList]
        }
        if (productDetails?._id) {
            body._id = productDetails?._id;
        }
        await dispatch(createProductCall(body));

        console.log("productDetails33", productDetails)

        // { 
        //     "name":"Nodular Cast Iron",
        //      "title":"Nodular Cast Iron",
        //       "description":"Cast iron is a group of ironcarbon alloys with a carbon content more than 2%. Its usefulness derives from its relatively low melting temperature",
        //        "imageurl":"",
        //         "status":true,
        //          "type":"Iron",
        //           "category":"metal",
        //            "price":60,
        //            "priceType":"KG"
        //  }
    }
    const validateImageFormat = (type) => {
        if (type == 'image/jpeg' || type == 'image/png') {
            return true;
        } else {
            //   showUserMessage("Please upload Jpeg/Png images only.");
            return false;
        }
    };
    const imageChange = async (e) => {

        if (e.target.files.length) {
            if (validateImageFormat(e.target.files[0].type)) {
                const filesName = e.target.files[0];
                setCoverImage(URL.createObjectURL(e.target.files[0]))
                var FR = new FileReader();

                FR.addEventListener("load", function (ev) {
                    setCoverImage(ev.target.result)
                });

                FR.readAsDataURL(filesName);


                // this.setState({image:{
                //     coverImage: URL.createObjectURL(e.target.files[0])
                // }});
                // const respApp = await uplaodImageNew(filesName, 'collection');
                // this.props.handleInputChangeforImageCall('coverImage',respApp?.imageUrl)    
            }
        }
    };
    const handleDeleteImg = () => {
        setCoverImage(null);
    }
    const onConformPage = () => {
        setProductSuccess(false)
        dispatch(setproductDetailsResponse({}));
        if (props?.match?.params?.id) {
            history.push("/admin/manageProducts");
        } else {
            window.location.reload();
        }

    }
    const handleAddClick = () => {
        setInputList([...inputList, { city: "", client: "", price: "", discount: "", cityError: false, clientError: false, priceError: false, discountError: false }]);
    };
    const handleInputChange = (e, index) => {
        const { name, value } = e.target;
        const list = [...inputList];
        list[index][name] = value;
        const name1 = name + "Error";
        list[index][name1] = false;
        setInputList(list);
    };
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };
    const priceSectionValidate = () => {
        let returnFlag = true;
        const list = [...inputList];
        inputList.map((item, index) => {
            console.log(item);
            if (item?.city === "") {
                returnFlag = false;
                list[index]['cityError'] = true;
            }
            if (item?.client === "") {
                returnFlag = false;
                list[index]['clientError'] = true;
            }
            if (item?.discount === "") {
                returnFlag = false;
                list[index]['discountError'] = true;
            }
            if (item?.price === "") {
                returnFlag = false;
                list[index]['priceError'] = true;
            }
        })
        setInputList(list);
        return returnFlag;
    }

    const allCitys = [{ id: 45, name: "Bangalore" }, { id: 45, name: "Hyderabad" }, { id: 45, name: "Mumbai" }, { id: 45, name: "Delhi" }]
    const allClients = [{ id: 45, name: "ABC Company" }, { id: 45, name: "AB Company" }, { id: 45, name: "A Company" }, { id: 45, name: "ABCD Company" }]
    const allCategories = [
        {name:"Fresh Vegetables", value:"Fresh Vegetables"},
        {name:"Fresh Fruits", value:"Fresh Fruits"},
        {name:"Dairy Products", value:"Dairy Products"},
        {name:"Frozen foods", value:"Frozen foods"},
      ];
    return (
        <>
            <div className="freshbag-wrapper">
                <div className="product-upload-section">
                    <div className="product-upload-head">
                        <h3> Create Product</h3>
                    </div>
                    <div className="product-upload-body">
                        <Form.Group size="lg" controlId="title">
                            <Form.Label>Title *</Form.Label>
                            <Form.Control
                                autoFocus
                                name="title"
                                type="text"
                                value={productData?.title}
                                onChange={(e) => handleChangeValue(e)}
                                isInvalid={errorData?.title}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errorData?.title ? <>
                                    {errorDataValue?.title}
                                </> : null}
                            </Form.Control.Feedback>
                        </Form.Group>
                        <Form.Group size="lg" controlId="description">
                            <Form.Label>Description *</Form.Label>
                            <textarea class="form-control"
                                name="description"
                                value={productData?.description}
                                onChange={(e) => handleChangeValue(e)}

                                id="textAreaExample1" rows="4"></textarea>


                            <Form.Control.Feedback type="invalid">
                                {errorData?.description ? <>
                                    {errorDataValue?.description}
                                </> : null}
                            </Form.Control.Feedback>
                        </Form.Group>


                        <Form.Group size="lg" controlId="category">
                            <Form.Label>Category *</Form.Label>
                            <Form.Control
                                as="select"
                                onChange={(e) => handleChangeValue(e)}
                                name='category'
                                value={productData.category}
                                isInvalid={errorData?.category}
                            >
                                <option value="" key="">Select</option>

                                {(allCategories || [])?.map((cateD) => (
                                    <option value={cateD.value} key={cateD.value}>
                                        {cateD.value}
                                    </option>
                                ))}

                            </Form.Control>
                            <Form.Control.Feedback type="invalid">
                                {errorData?.category ? <>
                                    {errorDataValue?.category}
                                </> : null}
                            </Form.Control.Feedback>
                        </Form.Group>


                        {/* <Form.Group size="lg" controlId="price">
                            <Form.Label>Price *</Form.Label>
                            <Form.Control
                                type="text"
                                name="price"
                                value={productData?.price}
                                onChange={(e) => handleChangeValue(e)}
                                isInvalid={errorData?.price}
                            />
                            <Form.Control.Feedback type="invalid">
                                {errorData?.price ? <>
                                    {errorDataValue?.price}
                                </> : null}
                            </Form.Control.Feedback>
                        </Form.Group> */}

                        <Form.Group size="lg" controlId="email">
                            <Form.Label>Image *</Form.Label>
                            <Row className="imageContainer imageContainer">
                                <Row className="imageRow">

                                    {(coverImage) ? (<>
                                        <DeleteBin className="deleteImgIcon"
                                            onClick={(image) => {
                                                handleDeleteImg(image)
                                            }}
                                        />
                                        <img src={coverImage} alt="Preview" className="previewImageCollection" />
                                    </>) : (
                                            <label htmlFor="uploadButton">
                                                <span className="labelStyles">
                                                    <ImagePlaceholderIcon />
                                                    <p className="uploadImageBtn">Upload image</p>
                                                </span>
                                            </label>)
                                    }

                                    {!coverImage && (<input
                                        id="uploadButton"
                                        type="file"
                                        accept="image/*"
                                        style={{ display: "none" }}
                                        // className="uploadImageBtn"
                                        onChange={imageChange}
                                    />)}
                                </Row>
                            </Row>
                            <Form.Control.Feedback type="invalid">
                                {errorImage ? <>
                                    {errorImage}
                                </> : null}
                            </Form.Control.Feedback>
                        </Form.Group>
                        <Form.Group size="lg" controlId="priceType">
                            <Form.Label>Price Type *</Form.Label>
                            <Form.Control
                                as="select"
                                onChange={(e) => handleChangeValue(e)}
                                name='priceType'
                                value={productData.priceType}
                                isInvalid={errorData?.priceType}
                            >
                                <option value="" key="">Select</option>

                                {(allPriceType || [])?.map((priceTypeD) => (
                                    <option value={priceTypeD.id} key={priceTypeD.id}>
                                        {priceTypeD.value}
                                    </option>
                                ))}

                            </Form.Control>
                            <Form.Control.Feedback type="invalid">
                                {errorData?.priceType ? <>
                                    {errorDataValue?.priceType}
                                </> : null}
                            </Form.Control.Feedback>
                        </Form.Group>
                        <div className="priceSection">
                            {inputList.map((x, i) => {
                                return (<>
                                    <div className="singleRowSection">
                                        <Form.Group className="price_btn_lm" size="lg" >
                                            <Form.Label>City</Form.Label>
                                            <Form.Control
                                                name="city"
                                                isInvalid={x.cityError}
                                                value={x.city}
                                                onChange={e => handleInputChange(e, i)} as="select" >
                                                <option name="">Select</option>
                                                {(allCitysList || []).map((item, key) => {
                                                    // const selecVAl =  (LotData?.categoryId==item.id) ? true : false;
                                                    return (
                                                        <>
                                                            <option selected={false} name={item.id} value={item.name}>{item.name}</option>
                                                        </>)
                                                })}
                                            </Form.Control>

                                        </Form.Group>
                                        <Form.Group className="price_btn_lm" size="lg" >
                                            <Form.Label>Client</Form.Label>

                                            <Form.Control
                                                as="select"
                                                name="client"
                                                isInvalid={x.clientError}
                                                value={x.client}
                                                onChange={e => handleInputChange(e, i)}
                                            >
                                                <option name="">Select</option>
                                                {(allClientList || []).map((item, key) => {
                                                    // const selecVAl =  (LotData?.categoryId==item.id) ? true : false;
                                                    return (
                                                        <>
                                                            <option selected={false} name={item.id} value={item.name}>{item.name}</option>
                                                        </>)
                                                })}
                                            </Form.Control>

                                        </Form.Group>
                                        <Form.Group className="price_btn_sm" size="lg" >
                                            <Form.Label>Price</Form.Label>
                                            <Form.Control
                                                autoFocus
                                                type="text"
                                                name="price"
                                                isInvalid={x.priceError}
                                                value={x.price}
                                                onChange={e => handleInputChange(e, i)}
                                            // isInvalid={errorData?.firstName}
                                            />
                                        </Form.Group>
                                        <Form.Group className="price_btn_sm" size="lg" >
                                            <Form.Label>Discount(%)</Form.Label>
                                            <Form.Control
                                                type="text"
                                                name="discount"
                                                isInvalid={x.discountError}
                                                value={x.discount}
                                                onChange={e => handleInputChange(e, i)} />
                                        </Form.Group>
                                        <div className="actionRowItem">
                                            {i !== 0 && (
                                                <DeleteBin className="deleteImgIcon"
                                                    onClick={() => { handleRemoveClick(i) }}
                                                />
                                            )
                                            }

                                        </div>
                                    </div>
                                </>)
                            })}


                            <div className="add-new-price-btn-section">
                                <Button onClick={handleAddClick}>Add new Price</Button>
                            </div>
                        </div>

                        <div className="submit-btn-section">
                        <Button disabled={loading} onClick={createAccountData}>{loading ? "Loading" : "Save"}</Button>
                        </div>

                    </div>
                </div>
                <CustomModal
                    show={productSuccess}
                    titleText={textHeader}
                    deleteText="ok"
                    cancelText=""
                    onCancel={() => { }}
                    onDelete={() => { onConformPage() }}
                />
            </div>
        </>
    );
}

export default CreateProduct;
