import React, { Component, useEffect, useState } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { useHistory, useLocation } from "react-router-dom";
import * as ReactPopOver from "react-popover";
import Logo from "../../img/main_logo.png";
import { Nav, Container, Form, FormControl, Dropdown } from "react-bootstrap";
import Navbar from "react-bootstrap/Navbar";
import { isMobile } from "react-device-detect";

import { Row, Col, Button, CardDeck, CardColumns } from "react-bootstrap";
import "./styles.css";
// import 'bootstrap/dist/css/bootstrap.min.css';
import bag from "../../img/bag.png";
import DropdownList from "./DropdownList";
import { updateUserDetails } from "../../redux/slices/auth/authSlice";
const Header = (props) => {
  const { cartItems } = useSelector(({ productCart }) => productCart);
  const location = useLocation();
  const history = useHistory();
  const dispatch = useDispatch();
  const { error, loading, user } = useSelector(({ auth }) => auth);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {}, []);
  const gotoCartPage = (url) => {
    setIsOpen(false);
    history.push(url);
  };
  const getShortName = () => {
    var shortname = "";
    try {
      var firstName = user?.data?.firstName
        ? user?.data?.firstName.charAt(0).toUpperCase()
        : "";
      var lastName = user?.data?.lastName
        ? user?.data?.lastName.charAt(0).toUpperCase()
        : "";
      shortname = firstName + lastName;
    } catch (error) {}
    return shortname;
  };
  const togglePopover = () => {
    setIsOpen(!isOpen);
  };
  const handleLogout = () => {
    localStorage.clear();
    history.push("/products");
    setIsOpen(!isOpen);
    dispatch(updateUserDetails(null));
  };
  const Links = [
    { link: "/all-trips", label: "Experiential Programs" },
    { link: "/upskill-programs", label: "UpSkill Programs" },
    { link: "/learning-resources", label: "Learning resources" },

    { link: "/ourExperts", label: "Our Experts" },
    // { link: "/aboutUs", label: "About Us" },
    // { link: "/contactus", label: " Contact Us" },
  ];
  console.log("location", location);
  const urlL = location?.pathname;
  return (
    <>
      <div className="header-wrapper" id="headerWrapper">
        <div className="headerSection">
          <div className="collapse navbar-collapse" id="navbarCollapse">
            <div className="allItemsSection">
              <div className="leftsideItem logo-section">
                <img
                  className="mainLogoImage"
                  onClick={() => {
                    gotoCartPage("/home");
                  }}
                  src={Logo}
                ></img>
                {/* <span
                  onClick={() => {
                    gotoCartPage("/home");
                  }}
                >
                  Pragyan Academy of Learning
                </span> */}
              </div>

              <div className="rightsideItem">
                {isMobile ? (
                  <Container>
                    <Navbar expand="lg">
                      <Navbar.Toggle
                        aria-controls="basic-navbar-nav"
                        className="border-0 p-0"
                      />
                      <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="mx-auto alignCenter">
                          {Links?.map((item) => {
                            var isSelected = urlL === item?.link ? true : false;
                            if (
                              item?.link === "/upskill-programs" &&
                              !isSelected
                            ) {
                              isSelected =
                                urlL === "/" || urlL === "/home" ? true : false;
                            }
                            return (
                              <Nav.Link href={item?.link} className="linksSell">
                                {item?.label}
                              </Nav.Link>
                            );
                          })}
                        </Nav>
                      </Navbar.Collapse>

                      {/* <LoginPopup /> */}
                    </Navbar>
                  </Container>
                ) : (
                  <>
                    {Links?.map((item) => {
                      var isSelected = urlL === item?.link ? true : false;
                      if (item?.link === "/upskill-programs" && !isSelected) {
                        isSelected =
                          urlL === "/" || urlL === "/home" ? true : false;
                      }
                      return (
                        <button
                          onClick={() => {
                            gotoCartPage(item?.link);
                          }}
                          className={
                            isSelected
                              ? "signInButton selected"
                              : "signInButton"
                          }
                        >
                          {item?.label}
                        </button>
                      );
                    })}
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Header;
