import React, { useEffect, useState } from "react";
import { TripCardListStyled } from "./TripCardList.styled";
const TripCardList = (props: any) => {
  const { handleClickButton, list, section } = props;

  return (
    <>
      <TripCardListStyled>
        <div className={`list-view-all list-view-all-${section}`}>
          {list?.map((item: any) => {
            const isUpSkill = item?.skills?.length > 0 ? true : false;
            return (
              <div className="list-view">
                <div className="list-view-img">
                  <div
                    className="list-view-img-con"
                    onClick={() => {
                      handleClickButton(item);
                    }}
                  >
                    <img src={item?.img} />{" "}
                  </div>
                </div>
                <div className="list-view-content">
                  <h4>{item?.title}</h4>
                  {item?.skills?.length > 0 ? (
                    <>
                      <div className="list-view-skills">
                        <ul>
                          {item?.skills.map((skill: any) => {
                            return <li>{skill?.label}</li>;
                          })}
                        </ul>
                      </div>
                    </>
                  ) : (
                    <>
                      <p>{item?.text}</p>
                      {item?.text1 && <p>{item?.text1}</p>}
                    </>
                  )}

                  <div className="button-list-view">
                    <button
                      onClick={() => {
                        handleClickButton(item);
                      }}
                    >
                      {isUpSkill ? "View Full Details" : "Discover this tour"}
                    </button>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </TripCardListStyled>
    </>
  );
};

export default TripCardList;
