import { createAsyncThunk } from "@reduxjs/toolkit";
import { get, post, put, del } from "../../../library/Requests/helpers";
import { SERVER_IP, API_BASE } from "./../../../config";

export const getallAddress = createAsyncThunk(
  "productCart/getallAddress",
  async (id: any) => await get(`${SERVER_IP}/user/address/getAllAddressByUserId/${id}`)

);

export const addAddressCall = createAsyncThunk(
  "auth/placeOrderCall",
  async (body: any) => {
    if(body?._id){
      return await put(`${SERVER_IP}/user/address/update`, body)   
    }else{
      return await post(`${SERVER_IP}/user/address/create`, body)   
    }
    
  }
);


export const deleteAddressCall = createAsyncThunk(
  "productCart/deleteAddressCall",
  async (id: any) => await del(`${SERVER_IP}/user/address/delete/${id}`)

);
