import React, { useEffect, useState } from "react";
import singapore_img1 from "../../img/singapore_img1.png";
import { TripCardGridStyled } from "./TripCardGrid.styled";
const TripCardGrid = (props: any) => {
  const { handleClickButton, list, section } = props;

  return (
    <>
      <TripCardGridStyled>
        <div className={`grid-view-all grid-view-all-${section}`}>
          {list?.map((item: any) => {
            const isUpSkill = item?.skills?.length > 0 ? true : false;

            return (
              <div className="grid-view">
                {item?.img && (
                  <div className="grid-view-img">
                    <div
                      className="grid-view-img-con"
                      onClick={() => {
                        handleClickButton(item);
                      }}
                    >
                      <img src={item?.img} />{" "}
                    </div>
                  </div>
                )}
                <div className="grid-view-content">
                  <h4>{item?.title}</h4>
                  {isUpSkill ? (
                    <>
                      <p>{item?.text}</p>
                      <ul>
                        {item?.skills.map((skill: any) => {
                          return <li>{skill?.label}</li>;
                        })}
                      </ul>
                      <div className="button-grid-view">
                        <button
                          onClick={() => {
                            handleClickButton(item);
                          }}
                        >
                          View Full Details
                        </button>
                      </div>
                    </>
                  ) : (
                    <>
                      {" "}
                      <p>{item?.text}</p>
                      <div className="button-grid-view">
                        <button onClick={handleClickButton}>
                          Discover this tour
                        </button>
                      </div>
                    </>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      </TripCardGridStyled>
    </>
  );
};

export default TripCardGrid;
