

import React, { useEffect, useState } from "react";

import { Row, Col, Button, Form, CardDeck, CardColumns } from "react-bootstrap";
import "./styles.css";
import { useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
// import ProductCard from "./../../components/ProductCard";

import { updateUserDetails } from "../../redux/slices/auth/authService";


const initVal = {
  firstName: undefined,
  lastName: undefined,
  phoneNumber: undefined,
  email: undefined,
  companyName: undefined,
  GSTNo: undefined,
  officeNo: undefined,
  officeEmail: undefined,
  PANNo: undefined,
  companyRegNo: undefined,
  city: undefined,
};

const initValError = {
  firstName: false,
  lastName: false,
  phoneNumber: false,
  email: false,
  companyName: false,
  GSTNo: false,
  officeNo: false,
  officeEmail: false,
  PANNo: false,
  companyRegNo: false,
  city: false,

};
const UserProfile = () => {
  const [signUpData, setSignUpData] = useState(initVal);
  const [errorData, setErrorData] = useState(initValError);

  const [addressEnabled, setAddressEnabled] = useState(false)
  const history = useHistory();
  const dispatch = useDispatch();
  const { allProducts } = useSelector(({ productCart }) => productCart);
  const { error, loading, user } = useSelector(({ auth }) => auth);
  const [isProfileEdit, setIsProfileEdit] = useState(false);

  useEffect(() => {
  }, []);
  useEffect(() => {
    const initVal = {
      firstName: user?.data?.firstName,
      lastName: user?.data?.lastName,
      phoneNumber: user?.data?.phoneNumber,
      email: user?.data?.email,
      companyName: user?.data?.companyName,
      city: user?.data?.city,
      GSTNo: user?.data?.GSTNo,
      officeNo: user?.data?.officeNo,
      officeEmail: user?.data?.officeEmail,
      PANNo: user?.data?.PANNo,
      companyRegNo: user?.data?.companyRegNo,

    };
    setSignUpData(initVal);
    setErrorData(initValError);
  }, [user?.data]);
  const handleUpdate = (e) => {
    const { name, value } = e.target;
    var value1 = value;
    if (value.trim() == "") {
      value1 = value.trim();
    }
    const addr = { ...signUpData };
    const addr_error = { ...errorData };
    if (name == 'phoneNumber' || name == "officeNo") {
      const re = /^[0-9\b]+$/;
      if (value1 === '' || re.test(value1)) {
        setSignUpData({
          ...addr,
          [name]: value1,
        });
        setErrorData({
          ...addr_error,
          [name]: false,
        });
      } else {
        setErrorData({
          ...addr_error,
          [name]: true,
        });
      }
    } else if (name === "email" || name === "officeEmail") {

      setSignUpData({
        ...addr,
        [name]: value1,
      });
      const emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (emailRegex.test(String(value).toLowerCase())) {
        setErrorData({
          ...addr_error,
          [name]: false,
        });
      } else {
        setErrorData({
          ...addr_error,
          [name]: true,
        });
      }


    } else {
      setSignUpData({
        ...addr,
        [name]: value1,
      });
      setErrorData({
        ...addr_error,
        [name]: false,
      });
    }
  }
  const saveUserDetails = () => {
    var isValid = true;
    var errornew = { ...errorData };
    for (var key in signUpData) {
      if (signUpData[key] === "" || !signUpData[key]) {
        errornew[key] = true;
        isValid = false;
      }
    }
    setErrorData({ ...errornew });
    if (isValid) {
      const body = { "_id": user?.data?._id, ...signUpData }
      dispatch(updateUserDetails(body));
      
    }
  }
  return (
    <>
      <div className="userProfile-wrapper">
        <div className="profileinfoHeader">
          <span>Profile Information</span>
          {isProfileEdit ?
            <span className="profileEdit" onClick={() => { setIsProfileEdit(false) }}>Cancel</span>
            :
            <span className="profileEdit" onClick={() => { setIsProfileEdit(true) }}>Edit Profile</span>
          }

        </div>
        <div>
          <span className="headerText">User Name</span>
        </div>
        <div className="namesSection">
          <input
            value={signUpData?.firstName}
            disabled={!isProfileEdit}
            type="text"
            className={errorData?.firstName && ("hasError")}
            name="firstName"
            onChange={handleUpdate}
          />
          <input
            value={signUpData?.lastName}
            disabled={!isProfileEdit}
            type="text"
            className={errorData?.lastName && ("hasError")}
            name="lastName"
            onChange={handleUpdate}
          />

        </div>
        <div className="user-details-row">
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">Email Address</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.email}
                disabled={true}
                className={errorData?.email && ("hasError")}
                type="text"
                name="email"
                onChange={handleUpdate}
              />
            </div>
          </div>
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">Mobile Number</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.phoneNumber}
                disabled={!isProfileEdit}
                className={errorData?.phoneNumber && ("hasError")}
                type="text"
                onChange={handleUpdate}
                name="phoneNumber"
              />
            </div>
          </div>
        </div>
        <div className="user-details-row">
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">Company Name</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.companyName}
                disabled={true}
                className={errorData?.companyName && ("hasError")}
                type="text"
                onChange={handleUpdate}
                name="companyName"
              />
            </div>
          </div>
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">GSTNo</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.GSTNo}
                disabled={!isProfileEdit}
                className={errorData?.GSTNo && ("hasError")}
                type="text"
                onChange={handleUpdate}
                name="GSTNo"
              />
            </div>
          </div>
        </div>
        <div className="user-details-row">
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">City</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.city}
                disabled={true}
                className={errorData?.city && ("hasError")}
                type="text"
                onChange={handleUpdate}
                name="city"
              />
            </div>
          </div>
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">Office Email</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.officeEmail}
                disabled={!isProfileEdit}
                className={errorData?.officeEmail && ("hasError")}
                type="text"
                onChange={handleUpdate}
                name="officeEmail"
              />
            </div>
          </div>
        </div>
        <div className="user-details-row">
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">PAN No</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.PANNo}
                disabled={!isProfileEdit}
                className={errorData?.PANNo && ("hasError")}
                type="text"
                onChange={handleUpdate}
                name="PANNo"
              />
            </div>
          </div>
          <div>
            <div className="profileHeaderSec">
              <span className="headerText">Company Reg No</span>
            </div>
            <div className="namesSection">
              <input
                value={signUpData?.companyRegNo}
                disabled={!isProfileEdit}
                className={errorData?.companyRegNo && ("hasError")}
                type="text"
                onChange={handleUpdate}
                name="companyRegNo"
              />
            </div>
          </div>
        </div>
        {isProfileEdit && (
          <div className="profileEditSave">
            <Button onClick={saveUserDetails}>Save</Button>
          </div>

        )}
      </div>
    </>
  );
}

export default UserProfile;
