

import React, { useEffect, useState } from "react";

import { Row, Col, Button, Form, CardDeck, CardColumns } from "react-bootstrap";
import "./styles.css";
import { useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import ProductItem from "./ProductItem";

import {setallProductsData } from "../../redux/slices/productCart/productCartSlice";
import { getallProducts, deleteProduct } from "../../redux/slices/productCart/productCartService";
import CustomModal from "../../components/CustomModel";
import { createProductCall } from "../../redux/slices/productCart/productCartService";


const ManageProducts = ( ) => {
  
  const [addressEnabled, setAddressEnabled ] = useState(false)
  const [showDeleteWarning, setShowDeleteWarning] = useState(false);
  const [deleteProductData, setDeleteProductData] = useState({});

  const history = useHistory();
  const dispatch = useDispatch();
  const { allProducts } = useSelector( ({ productCart }) => productCart );
  useEffect(() => {
    dispatch(getallProducts({}));
    console.log("vinod");
  }, []);
  const onDeleteClick = (product)=>{
    setDeleteProductData(product)
    setShowDeleteWarning(true);
  }
  const deleteButtonClick = async ()=>{
    setShowDeleteWarning(false);
    if(deleteProductData?._id){
      await dispatch(deleteProduct(deleteProductData?._id));
      dispatch(getallProducts({}))
    }
}
const cancelButtonClick =()=>{

  setShowDeleteWarning(false);
}
const updateStatus = async (e, product)=>{
  console.log("e", e.target.checked);
  if(product?._id){
    const body = {
      "status": e.target.checked,
      "_id" : product?._id,
  
      // "priceType": "KG"
  }
  console.log("product", body);
  await dispatch(createProductCall(body));
  dispatch(getallProducts({}));
  }
 

}
  console.log("allProducts", allProducts)
  return (
      <>
        <div className="freshbag-wrapper">
          <div className="freshbag-wrapper-text">
            <div className="freshbag-container">
              <div className="products-list">

                {(allProducts || []).map((product, index)=>{
                  return (
                    <ProductItem  updateStatus={updateStatus} onDeleteClick ={onDeleteClick} fromHome={true} key={index} product={product}/>
                    
                  )
                })}
                                
              </div>
            </div>
          </div>
          <CustomModal
                show={showDeleteWarning}
                titleText={`Are you sure you want to delete the Product?`}
                deleteText="Delete"
                cancelText="Cancel"
                onCancel={() => {cancelButtonClick()}}
                onDelete={() => {deleteButtonClick()}}
            />
    </div>
      </>
    );
  }

export default ManageProducts;
