import { createAsyncThunk } from "@reduxjs/toolkit";
import { get, post, put, del } from "../../../library/Requests/helpers";
import { SERVER_IP, API_BASE } from "./../../../config";

export const getallProducts = createAsyncThunk(
  "productCart/getallProducts",
  async (body: any) => await post(`${SERVER_IP}/products/all`,body )

);

export const createProductCall = createAsyncThunk(
  "auth/createProductCall",
  async (body: any) => {
    if(body?._id){
      return await put(`${SERVER_IP}/user/products/update`, body)
    }else{
      return await post(`${SERVER_IP}/user/products/create`, body)
    }
   
  }
);

export const deleteProduct = createAsyncThunk(
  "auth/deleteProduct",
  async (id: any) => await del(`${SERVER_IP}/user/products/delete/${id}`)
);
export const getProductById = createAsyncThunk(
  "productCart/getProductById",
  async (id: any) => await get(`${SERVER_IP}/user/products/getProductById/${id}`)
);

export const placeOrderCall = createAsyncThunk(
  "auth/placeOrderCall",
  async (body: any) => {
      return await post(`${SERVER_IP}/user/order/create`, body)   
  }
);
export const getOrderDetails = createAsyncThunk(
  "productCart/getOrderDetails",
  async (id: any) => await get(`${SERVER_IP}/user/order/getOrderById/${id}`)
);
export const getallOrderCall = createAsyncThunk(
  "productCart/getallOrderCall",
  async (id: any) => await get(`${SERVER_IP}/user/order/getAllOrderByUserId/${id}`)
);

export const getallOrdersCall = createAsyncThunk(
  "productCart/getallOrdersCall",
  async (id: any) => await get(`${SERVER_IP}/user/order/getAllOrder`)
);


export const getAllUsers = createAsyncThunk(
  "productCart/getAllUsers",
  async (id: any) => await get(`${SERVER_IP}/user/getAllUsers`)
);


