import { Modal, Button } from "react-bootstrap";
import React, { useEffect, useState } from "react";
import { triggerMixpanelEvent } from "./../../Scenes/common";
import { FinTechApplyNowStyled } from "./FinTechApplyNow.styled";

import ContactUS from "./../../pages/ContactUS";
const FinTechApplyNow = (props: any) => {
  const { handleHide, handleSucces } = props;
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [message, setMessage] = useState("");
  const [errorEmail, setErrorEmail] = useState("");
  const [thankyouMessage, setThankyouMessage] = useState("");
  const [emptyName, setEmptyName] = useState(false);
  const [emptyEmail, setEmptyEmail] = useState(false);
  const [emptyPhoneNumber, setEmptyPhoneNumber] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const formSubmitClick = async () => {
    setThankyouMessage("");
    if (name != "" && email != "" && phoneNumber != "" && errorEmail === "") {
      var bostStr = `name=${name},email=${email},phoneNumber=${phoneNumber},message=${message}, `;
      const respJson = { name, email, phoneNumber, message };
      triggerMixpanelEvent("fin-tech-applied", respJson);
      try {
        setName("");
        setEmail("");
        setPhoneNumber("");
        setMessage("");
        handleSucces();
        // setThankyouMessage("Thank you, we will get back to you shortly.");

        // const body ={
        //   body: bostStr,
        //   subject: "Talk To Us Information "+name,
        //   to: "vinod@myorigami.co"
        // }
        // setIsLoading(true)
        // const response = await Axios.post("http://52.165.36.192:8080/api/Email/Send", body);
        //   console.log("vinod", response)
        //   setIsLoading(false)
        // if(response?.statusText === "OK"){
        //   setName("");setEmail("");setPhoneNumber("");setMessage("");
        //   setThankyouMessage("Thank you, we will get back to you shortly.")
        // }
        // return response.data;
      } catch (error) {
        console.log(error);
      }
    } else {
      if (name === "") setEmptyName(true);
      if (email === "") setEmptyEmail(true);
      if (phoneNumber === "") setEmptyPhoneNumber(true);
    }
  };

  const onChangeValue = (e: any) => {
    if (e.target.name === "email") {
      setEmail(e.target.value);
      var pattern = new RegExp(
        /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
      );
      if (!pattern.test(e.target.value)) {
        setErrorEmail("Please enter valid email address.");
      } else {
        setErrorEmail("");
      }
    }
  };

  return (
    <Modal
      {...props}
      //   size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      className="fin-tech-apply-now-form"
      onHide={handleHide}
    >
      <FinTechApplyNowStyled>
        <div>
          <div className="contact-us-sec consultnew getinTouchPage">
            {/* <h2>Talk To Us</h2> */}
            <h4>Ready to transform your career?</h4>
            <div className="contactformnew">
              <input
                type="text"
                className={emptyName ? "error" : ""}
                onChange={(e) => {
                  setName(e.target.value);
                  setEmptyName(false);
                }}
                id="name"
                value={name}
                name="name"
                placeholder="Name*"
              ></input>
              <input
                type="text"
                className={emptyEmail ? "error" : ""}
                onChange={(e) => {
                  onChangeValue(e);
                  setEmptyEmail(false);
                }}
                id="email"
                value={email}
                name="email"
                placeholder="Email*"
              ></input>
              {errorEmail != "" && <p className="emailError">{errorEmail}</p>}
              <input
                type="text"
                className={emptyPhoneNumber ? "error" : ""}
                maxLength={10}
                onChange={(e) => {
                  setEmptyPhoneNumber(false);
                  const re = /^[0-9\b]+$/;
                  if (e.target.value === "" || re.test(e.target.value)) {
                    setPhoneNumber(e.target.value);
                  }
                }}
                id="phoneNumber"
                value={phoneNumber}
                name="phoneNumber"
                placeholder="Phone Number*"
              ></input>
              <textarea
                onChange={(e) => {
                  setMessage(e.target.value);
                }}
                id="message"
                value={message}
                name="message"
                placeholder="Your Message"
              ></textarea>

              {thankyouMessage != "" && (
                <p className="successText">{thankyouMessage}</p>
              )}
              <div className="fin-tect-contact-actions">
                <button className="fin-tech-close-btn" onClick={handleHide}>
                  {" "}
                  Close
                </button>
                <button onClick={formSubmitClick}>
                  {isLoading ? "Loading..." : "Submit"}
                </button>
              </div>
            </div>
          </div>
        </div>
      </FinTechApplyNowStyled>
      {/* <Modal.Body className="modalBodyDefault"></Modal.Body> */}
    </Modal>
  );
};
export default FinTechApplyNow;
